.api {
    font-family: Source Han Sans CN;

    .card-top {
        width: 100%;
        display: flex;
        justify-content: space-between;
        margin-top: 0.26rem;

        .title {
            margin-left: 0.30rem;

            .main-title {
                font-size: 0.18rem;
                font-weight: 400;
                line-height: 0.18rem;
                color: #040E34;

            }

            .lat-title {
                // display: inline-block;
                font-size: 0.12rem;
                //line-height: 0.12rem;
                color: #9696A3;
                margin-top: 0.08rem;
            }
        }


        .create-btn {
            width: 1.06rem;
            height: 0.4rem;
            background: #5C5EE4;
            border-radius: 4px;
            color: #FFFFFF;
            font-size: 0.14rem;
            margin-right: 0.4rem;
            display: flex;
            justify-content: center;
            align-items: center;

            .create-icon {
                width: 0.14rem;
                margin-right: 0.08rem;
            }
        }


    }

    .card-main {
        margin-top: 0.36rem;

        .ant-table-thead>tr>th {
            background: #F2F2FD;
            color: #9696A3;
            font-size: 0.13rem;
            line-height: 1;
            padding: .13rem 0 .13rem .3rem;
        }

        .ant-table-cell-row-hover{
            background: #F0F0FD !important;
        }


        .ant-table-tbody>tr>td {
            padding: 0 0 0 0.3rem;
            height: 0.6rem;
            font-size: 0.14rem;
        }



        .ant-table-cell::before {
            height: 0px !important;
        }

        .column-name>td {
            padding-left: .2rem;
        }

        .colum-operation>tr>td {
            padding-right: .2rem;
        }

        .ip-status {
            font-size: 0.14rem;
            font-weight: 400;

            &:hover .status-operation {
                opacity: 1;
            }

            .status-text-active {
                color: #3699FF;
            }

            .status-text-unactive {
                color: #DBDBDD;
            }

            .status-operation {
                color: #5C5EE4;
                margin-left: 0.06rem;
                opacity: 0;
            }

        }


        .more {
            color: #5C5EE4;
            font-size: 0.18rem;
            cursor: pointer;


        }

        .more-operation {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 0.32rem;
            height: 0.32rem;
            background: #F0F0FD;
            border-radius: 4px;

            // display: none;
            .more-edit,
            .more-delete {
                display: inline;
                font-size: 0.14rem;
                color: #5C5EE4;
            }

            .more-delete {
                margin-left: 0.2rem;
            }
        }
    }

    .modal-create-api {
        .modal-title {
            font-size: 0.18rem;
            font-weight: 400;
            line-height: 0.28rem;
            color: #040E34;
            margin-bottom: 0.32rem;
        }

        .modal-form {
            overflow: hidden;

            .status {
                color: #42C2FB;
                height: 0.14rem;
                font-size: 0.14rem;
            }

            .ant-input {
                background: #F2F2F7;
            }

            .modal-form-footer {
                width: 100%;
                justify-content: center;
                font-size: 0.14rem;
                letter-spacing: 0;

                .btn-ok,
                .btn-no {
                    border-radius: 4px;
                }

                .btn-ok {
                    background: #5C5EE4;
                    color: #ffffff;
                }

                .btn-no {
                    border: 1px solid #D9DBE1;
                    margin-left: 0.08rem;
                }
            }
        }
    }

    .modal-sure-api {
        .modal-title {
            font-size: 0.18rem;
            font-weight: 400;
            line-height: 0.28rem;
            color: #040E34;
            margin-bottom: 0.32rem;
        }

        .modal-msg {
            background: #F2F2F7;
            padding-bottom: 0.3rem;
            border-radius: 4px;

            .modal-msg-row {
                height: 20%;
                padding-top: 0.2rem;
                padding-left: 0.2rem;

                .row-name {
                    color: #9696A3;
                    font-size: 0.13rem;
                }

                .row-msg {
                    color: #525666;
                    font-size: 0.13rem;

                    .row-msg-r {
                        margin-left: 0.04rem;
                    }
                }
            }
        }

        .modal-form {
            margin-top: 0.2rem;

            .remind {
                .remind-main {
                    display: flex;
                    flex-direction: row;
                }

                .remind-text {
                    font-size: 13px;
                    font-weight: 400;
                    color: #525666;
                    margin-left: 0.07rem;

                    .remind-i {
                        color: #FFA800;
                    }
                }
            }

            .modal-form-footer {
                justify-content: center;

                .btn-ok {
                    height: 40px;
                    margin-top: 0.44rem;
                    background: #5C5EE4;
                    border-radius: 4px;
                    color: #FFFFFF;
                }
            }


        }

    }

    .modal-unopen {
        .modal-title {
            font-size: 0.18rem;
            font-weight: 400;
            line-height: 0.28rem;
            color: #040E34;
            margin-bottom: 0.24rem;
        }

        // .modal-remind{
        //     margin-bottom: 0.4rem;
        // }
        .modal-form {
            margin-top: 0.4rem;

            .text {
                margin-top: 0.16rem;
            }

            .ant-input {
                background: #F2F2F7;
            }

            .modal-form-footer {
                width: 100%;
                justify-content: center;
                font-size: 0.14rem;
                letter-spacing: 0;
                margin-top: 0.56rem;

                .btn-ok,
                .btn-no {
                    border-radius: 4px;
                }

                .btn-ok {
                    background: #5C5EE4;
                    color: #ffffff;
                }

                .btn-no {
                    border: 1px solid #D9DBE1;
                    margin-left: 0.08rem;
                }
            }
        }
    }

}

.swal2-popup {
    font-size: 0.2rem;
}

.ssh-cancel-button {
    background: #fff !important;
    color: #525666 !important;
    border: 1px solid #D9DBE1 !important;
}

.ssh-icon {
    color: #F64E60 !important;
    border-color: #F64E60 !important;
}
@primary-color: #5C5EE4;